import React, { useState, useEffect } from 'react';
import secureLocalStorage from 'react-secure-storage';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Play, Pause, Loader2, Volume2 } from 'lucide-react';
import { toast } from 'sonner';
import { VOICE_PROVIDERS } from '@/config/vapiProviders';
import axiosInstance from '@/api/axios/axiosInstance';

interface VoicePreviewButtonProps {
  previewUrl: string;
  voiceName: string;
  provider: string;
}

const VoicePreviewButton: React.FC<VoicePreviewButtonProps> = ({ previewUrl, voiceName, provider }) => {
  const [isPlaying, setIsPlaying] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [audio, setAudio] = useState<HTMLAudioElement | null>(null);
  const [hasError, setHasError] = useState(false);

  // Cleanup function
  const cleanup = () => {
    if (audio) {
      audio.pause();
      audio.removeEventListener('loadstart', handleLoadStart);
      audio.removeEventListener('canplay', handleCanPlay);
      audio.removeEventListener('ended', handleEnded);
      audio.removeEventListener('error', handleError);
      audio.removeEventListener('loadeddata', handleLoadedData);
      setAudio(null);
    }
  };

  // Event handlers
  const handleLoadStart = () => setIsLoading(true);
  const handleCanPlay = () => setIsLoading(false);
  const handleLoadedData = () => setIsLoading(false);
  const handleEnded = () => {
    setIsPlaying(false);
    setIsLoading(false);
  };
  const handleError = (e: Event) => {
    console.error('Audio error for', voiceName, 'from', provider, ':', e);
    setIsLoading(false);
    setIsPlaying(false);
    setHasError(true);
    toast.error(`Preview not available for ${voiceName} (${provider})`);
  };

  const handlePlayPause = async () => {
    if (isPlaying && audio) {
      audio.pause();
      setIsPlaying(false);
      return;
    }

    // Reset error state
    setHasError(false);

    try {
      setIsLoading(true);

      // Cleanup previous audio
      cleanup();

      // Validate URL
      if (!previewUrl || previewUrl.trim() === '') {
        throw new Error('No preview URL available');
      }

      // Check if this is a dynamic preview URL (starts with /api/voices/preview)
      if (previewUrl.startsWith('/api/voices/preview')) {
        // Handle dynamic preview generation
        await handleDynamicPreview(previewUrl);
      } else {
        // Handle static preview URLs
        await handleStaticPreview(previewUrl);
      }

    } catch (error) {
      console.error('Error playing voice preview:', error);
      setIsLoading(false);
      setIsPlaying(false);
      setHasError(true);

      // More specific error messages
      if (error instanceof Error) {
        if (error.message.includes('timeout')) {
          toast.error(`Preview timeout for ${voiceName}`);
        } else if (error.message.includes('No preview URL')) {
          toast.error(`No preview available for ${voiceName}`);
        } else if (error.message.includes('Voice preview not available')) {
          toast.error(`${voiceName} preview is currently unavailable`);
        } else {
          toast.error(`Preview unavailable for ${voiceName}`);
        }
      } else {
        toast.error(`Failed to play preview for ${voiceName}`);
      }
    }
  };

  // Handle dynamic preview generation
  const handleDynamicPreview = async (previewUrl: string) => {
    try {
      // Use axiosInstance which already has proper authentication setup
      const response = await axiosInstance.get(previewUrl);

      console.log('ðŸŽµ Voice preview response:', response.data);

      if (response.data.success && response.data.audioUrl) {
        // Play the generated audio
        console.log('âœ… Playing audio from:', response.data.audioUrl);
        await handleStaticPreview(response.data.audioUrl);
        toast.success(`Playing ${voiceName} from ${provider}`);
      } else if (response.data.note) {
        // Show informative message about preview status (don't throw error)
        console.log('â„¹ï¸ Showing note:', response.data.note);
        toast.info(response.data.note);
        setIsLoading(false);
        return; // Exit gracefully without throwing error
      } else if (response.data.message && !response.data.audioUrl) {
        // Show backend message when no audio URL
        console.log('â„¹ï¸ Showing message:', response.data.message);
        toast.info(response.data.message);
        setIsLoading(false);
        return; // Exit gracefully without throwing error
      } else {
        // Fallback error
        console.log('âŒ No audio URL or note found in response');
        throw new Error('Voice preview not available');
      }
    } catch (error: any) {
      if (error.response?.status === 401) {
        throw new Error('Authentication failed - please log in again');
      }
      throw error;
    }
  };

  // Handle static preview URLs
  const handleStaticPreview = async (audioUrl: string) => {
    const newAudio = new Audio();

    // Set up event listeners before setting src
    newAudio.addEventListener('loadstart', handleLoadStart);
    newAudio.addEventListener('canplay', handleCanPlay);
    newAudio.addEventListener('loadeddata', handleLoadedData);
    newAudio.addEventListener('ended', handleEnded);
    newAudio.addEventListener('error', handleError);

    // Configure audio settings
    newAudio.preload = 'metadata';
    newAudio.crossOrigin = 'anonymous';

    setAudio(newAudio);

    // Set source and load
    newAudio.src = audioUrl;
    newAudio.load();

    // Wait for audio to be ready
    await new Promise((resolve, reject) => {
      const timeout = setTimeout(() => {
        reject(new Error('Audio load timeout'));
      }, 10000); // 10 second timeout

      newAudio.addEventListener('canplay', () => {
        clearTimeout(timeout);
        resolve(void 0);
      }, { once: true });

      newAudio.addEventListener('error', () => {
        clearTimeout(timeout);
        reject(new Error('Audio load failed'));
      }, { once: true });
    });

    // Play the audio
    await newAudio.play();
    setIsPlaying(true);
    setIsLoading(false);
    toast.success(`Playing ${voiceName} from ${provider}`);
  };

  // Cleanup on unmount
  useEffect(() => {
    return cleanup;
  }, []);

  return (
    <Button
      variant="outline"
      size="sm"
      onClick={handlePlayPause}
      disabled={isLoading || hasError}
      className="w-full"
    >
      {isLoading ? (
        <Loader2 className="h-3 w-3 mr-1 animate-spin" />
      ) : isPlaying ? (
        <Pause className="h-3 w-3 mr-1" />
      ) : hasError ? (
        <Volume2 className="h-3 w-3 mr-1 opacity-50" />
      ) : (
        <Play className="h-3 w-3 mr-1" />
      )}
      {isLoading ? 'Loading...' : isPlaying ? 'Pause' : hasError ? 'Unavailable' : 'Preview'}
    </Button>
  );
};

const VoicePreviewTest: React.FC = () => {
  return (
    <div className="p-6 space-y-6">
      <div className="text-center">
        <h1 className="text-3xl font-bold">VAPI Voice Preview Test</h1>
        <p className="text-muted-foreground mt-2">Test all voice previews from VAPI providers</p>
      </div>

      {VOICE_PROVIDERS.map((provider) => (
        <Card key={provider.id} className="w-full">
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              {provider.name}
              <Badge variant="secondary">{provider.voices.length} voices</Badge>
            </CardTitle>
            <CardDescription>{provider.description}</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
              {provider.voices.map((voice) => (
                <Card key={voice.id} className="p-4">
                  <div className="space-y-2">
                    <div className="flex items-center justify-between">
                      <h4 className="font-medium">{voice.name}</h4>
                      <Badge variant="outline" className="text-xs">
                        {voice.gender}
                      </Badge>
                    </div>
                    <p className="text-sm text-muted-foreground">{voice.description}</p>
                    <div className="flex items-center gap-2 text-xs text-muted-foreground">
                      <span>{voice.language}</span>
                      {voice.accent && <span>â€¢ {voice.accent}</span>}
                    </div>
                    {voice.preview ? (
                      <VoicePreviewButton
                        previewUrl={voice.preview}
                        voiceName={voice.name}
                        provider={provider.name}
                      />
                    ) : (
                      <Button variant="outline" size="sm" disabled className="w-full">
                        No Preview Available
                      </Button>
                    )}
                  </div>
                </Card>
              ))}
            </div>
          </CardContent>
        </Card>
      ))}
    </div>
  );
};

export default VoicePreviewTest;
